import { LinksFunction } from "@remix-run/node";
import styles from "~/styles/store.css";
export const links: LinksFunction = () => [
  {
    rel: "stylesheet",
    href: styles,
  },
];

const Store = () => {
  const clickedItem = (item: ItemType, key: Number) => {
    console.log(item);
    const allItems = document.querySelectorAll(".item");
    const thisItem = allItems[key];
    console.log(thisItem);
  };

  const items: ItemType[] = [
    {
      id: 1,
      label: "First Item",
    },
    {
      id: 2,
      label: "Second Item",
    },
    {
      id: 3,
      label: "Third Item",
    },
    {
      id: 4,
      label: "Fourth Item",
    },
    {
      id: 5,
      label: "Fifth Item",
    },
    {
      id: 1,
      label: "First Item",
    },
  ];
  return (
    <div className="container">
      <div className="items">
        {items.map((item: ItemType, key) => (
          <div
            className="item"
            key={key}
            onClick={() => clickedItem(item, key)}
          >
            <div className="itemImage" />
            <div className="itemDetails">
              <div className="itemName">{item?.label}</div>
              <div className="itemPrice">$ 99.00</div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Store;
